import * as quizData from './quiz-data.json';

const musicUrl = 'https://open.spotify.com/embed/track/{id}';
const videoUrl = 'https://www.youtube.com/embed/{id}?autoplay=1';

export function getQuizData(index) {
    const quizItem = quizData[index - 1];

    return quizItem && {
        ...quizItem,
        music: musicUrl.replace('{id}', quizItem.music),
        video: videoUrl.replace('{id}', quizItem.video)
    }
}