import { getQuizData } from './quiz-data.service';

const quizId = location.search.split('=')[1];
const solutionButton = document.querySelector('.btn-response');

function getIframeContent(src) {
    const iframe = document.createElement('iframe');
    iframe.src = src;
    iframe.frameBorder = 0;
    iframe.allowtransparency = true;
    iframe.allow = "encrypted-media";
    iframe.autoplay = true;

    return iframe;
}

function getTitleContent(title) {
    const videoTitle = document.createElement('span');
    videoTitle.innerHTML = ` - ${title}`;

    return videoTitle;
}

const quizItem = getQuizData(quizId);

if (!quizItem) {
    solutionButton.style.display = 'none';
    return;
}

// Add music to player
document.querySelector('.music').appendChild(getIframeContent(quizItem.music));

// On Click show movie Information
solutionButton.addEventListener('click', (event) => {
    event.target.style.display = 'none';
    document.querySelector('.video').appendChild(getIframeContent(quizItem.video));
    document.querySelector('.video h1').appendChild(getTitleContent(quizItem.title));
});
